#header {
    width: 100%;
    height: 20%;
    background: #5e69ee;
    display: flex;
    flex-direction: column;
    border-radius: 25px 25px 0 0;
}

#header .level-1 {
    height: 50%;
    width: 100%;
    border-bottom: 1px rgba(0, 0, 0, .1) solid;
    padding: 2rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

a {
    text-decoration: none;
    display: block;
    padding: 10px;
}

a:hover {
    scale: 1.04;
}

#header .level-2 {
    height: 50%;
    width: 100%;
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: solid;
    border-image-slice: 1;
    border-width: 2px;
    border-image-source: linear-gradient(to left, #743ad5, #d53a9d);
}

#header .level-2 .perfil {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.perfil div p{
    margin: 0.2rem;
    color: #F4F4FB;
}

.icons-header {
    font-size: 2rem;
    color: '#F4F4FB';
}