#push-to-talk{
    height: 75%;
    display: flex;
    flex-direction: column;
    justify-items: center;
    align-items: center;
}

#push-to-talk div{
    width: 100%;
    padding: 10px;
    display: flex;
    justify-items: center;
    align-items: center;
}

#push-to-talk .btn{
    background: linear-gradient(-20deg, #b721ff 0%, #21d4fd 100%);
    color: #000;
    width: 400px;
    height: 400px;
    border: none;
    border-radius: 50%;
    transition: .4s;
}

#push-to-talk .btn:hover{
    transition: .4s;
    scale: 1.02;
    color: #fff;
}
.avatar{
    max-width: 56px;
    max-height: 56px;
}